.generic-card {
    $this: &;

    @include link-reset;

    display: block;

    &__media {
        @include objectFitImageParent;

        position: relative;
        overflow: hidden;
        transition: transform 0.3s ease-out;

        &::before {
            content: '';
            display: block;
            padding-top: percentage(350/304);

            @include desktop {
            padding-top: percentage(450/389);

            }
        }

        img {
            transition: transform 0.3s ease-out;
        }

        .generic-slider__card:nth-child(odd) & {
            .generic-slider--without-filters &,
            .generic-slider--image-gallery & {
                &::before {
                    padding-top: percentage(350/258);

                    @include desktop-medium {
                        padding-top: percentage(740/532);
                    }
                    
                }
            }
        }

        .generic-slider__card:nth-child(even) & {
            .generic-slider--without-filters &,
            .generic-slider--image-gallery & {
                &::before {
                    padding-top: percentage(508 /675);
                }
            }
        }

        @include desktop {
            .generic-slider__card:first-child & {
                .generic-slider--with-filters & {
                    &::before {
                        padding-top: percentage(450/818);
                    }
                }
            }
        }
    }

    &__content {
        margin-top: 24px;
        padding: 0 20px;
        color: var(--color-main-dark);

        @include desktop-medium {
            margin-top: 35px;
            padding: 0 40px;
        }
    }

    &__subtitle {
        margin-bottom: 8px;
        font-family: $ff-helvetica-neue;
        font-size: 8px;
        font-weight: $fw-regular;
        letter-spacing: 1.6px;
        text-transform: uppercase;

        @include desktop-medium {
            margin-bottom: 10px;
            font-size: 10px;
            line-height: 1.2;
            letter-spacing: 2px;
        }
    }

    &__title {
        position: relative;
        width: fit-content;
        font-family: $ff-financier-display;
        font-size: 22px;
        font-weight: $fw-regular;
        line-height: 1.2;
        letter-spacing: 1px;

        &:after {
            content: '';
            position: absolute;
            width: 100%;
            transform: scaleX(0);
            height: 1px;
            bottom: 0;
            left: 0;
            background-color:  var(--color-black);
            transform-origin: bottom right;
            transition: transform 0.25s ease-out;
        }

        @include desktop-medium {
            font-size: 30px;
        }
    }

    &__sup {
        font-family: $ff-financier-display;
        font-size: 16px;
        line-height: 1.2;
        font-style: italic;

        @include desktop-medium {
            margin-top: 10px;
            font-size: 18px;
        }

    }

    .generic-slider--with-filters &,
    .generic-slider--without-filters & {
        @include hover-focus {
            #{$this}__title {
                &:after {
                    transform: scaleX(1);
                    transform-origin: bottom left;
                }
            }

            & #{$this}__media img {
                transform: scale(1.07);
            }
            & #{$this}__media {
                transform: scale(0.97);
            }
        }
    }
}
