.generic-slider {
    $this: &;

    overflow: hidden;
    max-width: 100%;

    &__head {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 34px;
        
        #{$this}--with-filters & {
            @include until-desktop {
                margin-top: 30px;
            }

            @include desktop-medium {
                margin-bottom: 8px;
            }
        }
    }

    &__inner {
        @include until-desktop {
            display: flex;
            flex-direction: column;
            width: 100%;
        }

        #{$this}--without-filters &,
        #{$this}--image-gallery & {
            padding-top: 70px;
            padding-bottom: 70px;

            @include desktop-medium {
                padding-top: 90px;
                padding-bottom: 90px;
            }
        }
    }

    &__controls {
        grid-template-columns: auto auto;
        display: grid;
        width: max-content;
        gap: 20px;
        margin-left: auto;

        #{$this}--image-gallery & {
            margin-top: 35px;

            @include desktop-medium {
                margin-top: 60px;
                margin-bottom: 0;
            }
        }

        #{$this}--with-filters & {
            @include desktop-medium {
                margin-top: 10px;
            }
        }

        @include desktop {
            margin-top: 0;
            margin-bottom: 20px;
        }
    }

    &__title {
        font-family: $ff-financier-display;
        font-size: 30px;
        font-weight: $fw-light;
        line-height: 1.2;

        @include desktop-medium {
            font-size: 40px;
        }

        #{$this}--with-filters & {
            display: none;
        }
    }

    &__slider-container {
        max-width: 100%;
        margin-right: auto;
        margin-left: auto;

        @include tablet {
            max-width: 100%;
        }
    }

    &__list {
        display: flex;

        #{$this}--without-filters &,
        #{$this}--image-gallery & {
            align-items: center;
            margin-left: -35px;

            @include desktop {
                margin-left: 0;

            }
        }
    }

    &__card {
        box-sizing: content-box;
        padding-right: 20px;
        min-width: 304px;
        max-width: 304px;

        @include tablet {
            min-width: 389px;
            max-width: 389px;
        }

        @include desktop-medium {
            padding-right: 40px;
        }

        &:nth-child(odd) {
            #{$this}--without-filters &,
            #{$this}--image-gallery & {
                min-width: 258px;
                max-width: 258px;

                @include desktop-medium {
                    min-width: 532px;
                    max-width: 532px;
                }
            } 
        }

        &:nth-child(even) {
            #{$this}--without-filters &,
            #{$this}--image-gallery & {
                min-width: 320px;
                max-width: 320px;

                @include desktop-medium {
                    min-width: 675px;
                    max-width: 675px;
                }
            } 
        }

        #{$this}--without-filters &,
        #{$this}--image-gallery & {
            height: fit-content;
        }

        #{$this}--with-filters & {
            &:first-child {
                @include desktop {
                    min-width: 818px;
                    max-width: 818px;
                }
            }
        }
    }

    &--without-filters {
        background-color: $color-beige;
    }
}
