.category-list {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;

    @include desktop {
        margin-bottom: 10px;
    }

    &__list {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__item {
        & + & {
            &::before {
                content: '';
                display: inline-block;
                height: 0;
                width: 5px;
                border-top: 1px solid $color-black;
                margin: 0 7px 3px;

                @include desktop {
                    width: 56px;
                    margin: 0 14px 3px;
                }
            }
        }
    }

    &__link {
        text-decoration: none;
        text-transform: uppercase;
        font-family: $ff-helvetica-neue;
        letter-spacing: 2px;
        font-size: 10px;
        line-height: 1.4;

        @include linkhover($color-black);
    }
}
