.category-block {
    $this: &;
    padding-bottom: 5px;
    padding-top: 60px;


    @include desktop {
        display: flex;
        gap: 3%;
        flex-direction: row-reverse;
        padding-bottom: 100px;
        padding-top: 120px;
    }

    &__col {
        @include desktop {
            width: 57%;
        }
    }

    &__col-with-title {
        @include desktop {
            width: 40%;
        }
    }

    &__title-area {
        @include desktop {
            position: relative;
            overflow: hidden;
            &::before {
                content: '';
                display: block;
                padding-top: 34.2%;
            }
        }
    }

    &--three-columns {
        @include desktop {
            flex-direction: row;

            #{$this}__col,
            #{$this}__col-with-title {
                width: 31.33%;
            }
            #{$this}__title-area {
                &::before {
                    padding-top: 43.2%;
            }
            }
        }
    }
}

.catagory-block-container {
    & + & {
        .category-block {
            border-top: 1px solid $color-black;
        }
    }
}

