.section-video {
    $this: &;

    position: relative;
    padding: 72px 0 158px;
    background-color: #094545;
    border: 1px solid red;

    @include desktop-medium {
        padding: 125px 0 254px;
    }

    &__title {
        margin-bottom: 70px;
        font-family: $ff-helvetica-neue;
        font-size: 12px;
        font-weight: $fw-regular;
        line-height: 1.2;
        letter-spacing: 2.4px;
        text-align: center;
        text-transform: uppercase;
        color: $color-white;

        @include desktop-medium {
            margin-bottom: 114px;
        }
    }

    &__bg-element {
        position: absolute;
        bottom: 0;
        width: 100%;
        height: calc(50% + 30px / 2);
        padding-bottom: col(1);
        background: var(--theme-color-primary);

        @include desktop {
            height: calc(50% + #{col(1) / 2});
        }
    }

    &__container {
        @include container;

        position: relative;
        max-width: 1700px;

        @include desktop {
            padding-right: col(1);
            padding-left: col(1);
        }
    }
}
