.article-preview {
    $this: &;

    padding: 75px var(--container-gap);

    @include desktop {
        padding: 75px 0;
    }

    @include desktop-medium {
        max-width: 1100px;
    }

    @include desktop-wide {
        max-width: 1400px;
        padding: 170px 0;
    }

    &__inner {
        @include tablet {
            display: flex;
            align-items: center;
            justify-content: space-between;
            gap: 30px;
        }

        @include desktop {
            gap: 40px;
        }

        @include desktop-medium {
            gap: 100px;
        }

        @include desktop-wide {
            gap: 180px;
        }

    }

    &__content {
        margin-bottom: 60px;

        @include tablet {
            order: 2;
            width: 45%;
            margin-bottom: 0;
        }
    }

    &__media {
        @include objectFitImageParent;

        position: relative;
        transition: transform 0.3s ease-out;

        &::before {
            content: '';
            display: block;
            padding-top: percentage(400/304);

            @include tablet {
                padding-top: percentage(820/675);
            }
        }

        @include tablet {
            order: 1;
            width: 55%;
        }
    }

    &__label {
        display: block;
        margin-bottom: 20px;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        font-size: 8px;
        line-height: 1.2;
        letter-spacing: 1.6px;
        text-transform: uppercase;
    }

    &__title {
        margin-bottom: 20px;
        font-size: 35px;
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        line-height: 1.2;

        @include desktop-medium {
            margin-bottom: 35px;
            font-size: 55px;
        }
    }

    &__summary {
        margin-bottom: 35px;
        font-family: $ff-financier-display;
        font-weight: $fw-regular;
        line-height: 1.6;

        @include desktop-medium {
            margin-bottom: 45px;
            font-size: 18px;
        }
    }

    &__link {
        font-size: 12px;
        line-height: 1;
        text-transform: uppercase;
    }
}

