.featured-article {
    margin-bottom: 15px;
    @include desktop {
        display: flex;
        align-items: center;
        margin-bottom: 54px;
    }

    &__col1 {
        margin-bottom: 58px;

        @include desktop {
            width: percentage(435/1675);
            margin-left: percentage(140/1675);
            margin-right: percentage(140/1675);
            margin-bottom: 0;
        }
    }

    &__col2 {
        @include desktop {
            width: percentage(960/1675);
        }
    }

    &__media {
        @include objectFitImageParent;

        position: relative;
        overflow: hidden;
        z-index: 0;
        transition: transform 0.3s ease-out;

        &::before {
            content: '';
            display: block;
            padding-top: percentage(400/340);
            @include desktop {
                padding-top: percentage(960/985);
            }

        }

        img {
            transition: transform 0.3s ease-out;
        }
    }

    &__featured {
        display: block;
        font-family: $ff-helvetica-neue;
        font-weight: $fw-regular;
        font-size: 8px;
        letter-spacing: 1.6px;
        text-transform: uppercase;
        margin-bottom: 20px;
    }

    &__title {
        font-family: $ff-financier-display;
        font-weight: $fw-light;
        font-size: 35px;
        line-height: 1.14;
        margin-bottom: 15px;

        @include desktop {
            font-size: 55px;
            line-height: 1.14;
            margin-bottom: 27px;
        }
    }

    &__intro {
        font-family: $ff-financier-text;
        font-weight: $fw-regular;
        font-size: 18px;
        line-height: 1.55;
        margin-bottom: 30px;

        @include desktop {
            margin-bottom: 48px;
        }
    }

    &__btn {
        text-transform: uppercase;
    }
}
